function atSomeBody(noticeArr, ctn) {
  let newArr = [];
  let filterArr = new Set();
  let str = ctn;
  if (!noticeArr) {
    return false;
  }
  if (noticeArr instanceof Array) {
    // @的人去重
    noticeArr.forEach(x => {
      filterArr.add(x)
    });
    // set 转数组
    filterArr = Array.from(filterArr);
    for (let i = 0; i < filterArr.length; i++) {
      let atArr = filterArr[i].split('_');
      newArr.push(atArr);
    }

    // title 找到就变色并添加点击事件
    for (let j = 0; j < newArr.length; j++) {
      let findIndex = ctn.indexOf(newArr[j][1]);
      if (findIndex > -1) {
        let dom = document.createElement("a");
        dom.innerHTML = '@' + newArr[j][1];
        dom.style.color = "#0A66FF";
        dom.setAttribute('onclick', 'location.href="/isApp?id=' + newArr[j][0] + '"');
        str = str.replace(new RegExp('@' + newArr[j][1], "gim"), dom.outerHTML);
      }
      // 文字太长的话就显示省略并 添加‘全文’按钮
      if (ctn.length > 80) {
        str = str.slice(0, 1000);
        str += '<a class="seeMore" style="height: .4rem;width: .8rem;color: #136bfb;text-align: center;       font-size: .28rem;background: #fff;">...全文</a>'
      }
    }
    return str;
  }
  else {
    console.warn('@参数应为数组');
  }
}

export default atSomeBody;
